// src/hooks/useProductRatings.js
import { useQuery } from "@tanstack/react-query";

export function useProductRatings(productId) {
  return useQuery({
    queryKey: ["productRatings", productId], //    key 
    queryFn: async () => {
      if (!productId) return [];
      const res = await fetch(
        `https://maxim-test.courseszone-eg.com/api/products/${productId}/ratings`
      );
      if (!res.ok) throw new Error("  ");
      const data = await res.json();
      return data.data || []; //   
    },
    enabled: !!productId, //     productId 
    staleTime: 1000 * 60 * 5, // 5    
  });
}
